<script>
import Vue from 'vue'

import utils from '~/common/utils'
import Comment from '~/components/Comment'

export default {
  components: {
    // Comment
  },
  async asyncData({ $axios, params, error }) {
    let post
    try {
      post = await $axios.get('/api/tiaoji/' + params.id)
    } catch (e) {
      error({
        statusCode: 404,
        message: '话题不存在'
      })
      return
    }

    if (!post) {
      error({
        statusCode: 404,
        message: '话题不存在'
      })
      return
    }

    // console.log('post:' + JSON.stringify(post))

    const userScores = await $axios.get('/api/tiaoji/userscores/' + params.id)

    if (userScores) {
      userScores.sort((a, b) => {
        return b.total_score - a.total_score
      })
    }

    const [favorit] = await Promise.all([
      $axios.get('/api/favorite/favorited', {
        params: {
          entityType: 'tiaojiPost',
          entityId: post.post_id
        }
      })
    ])
    const favorited = favorit.favorited
    // console.log('favorited:' + JSON.stringify(favorit))

    return {
      post,
      userScores,
      favorited
    }
  },
  data() {
    return {
      href: ''
    }
  },
  computed: {
    isOwner() {
      return this.$store.state.user.current && this.post
    }
  },
  mounted() {
    this.href = window.location.href
  },
  updated() {},
  methods: {
    loading() {
      // if (this.$store.state.user.current) {
      //   const loader = this.$loading.show(
      //     {
      //       container: this.$refs.loadingContainer,
      //       isFullPage: false,
      //       canCancel: false,
      //       opacity: 0.95,
      //       backgroundColor: '#BEBEBE'
      //     },
      //     {
      //       default: this.$createElement('div', ['登录后才能查看'])
      //     }
      //   )
      // }
    },
    onCopy() {
      this.$toast.success('复制成功！可以粘贴分享啦')
      this.loading()
    },
    formatText2Html(description) {
      let html = description
      if (html) {
        html = html.replace(/ /g, '&nbsp;')
        html = html.replace(/\n/g, '<br/>')
      }
      return html
    },
    formatMajorsConntent() {
      let result = '-'
      if (this.post.major_top_class !== '') {
        result = this.post.major_top_class
      }

      if (this.post.major_top_class !== '') {
        result += '->' + this.post.major_second_class
      }

      if (this.post.major_top_class !== '') {
        result += '->' + this.post.major_third_class
      }

      return result
    },
    shareContent() {
      return (
        this.post.post_title.trim() +
        '\n  ' +
        this.post.description +
        '\n ———图片详情请点击链接：' +
        this.href
      )
    },
    async addFavorite(postId) {
      try {
        if (this.favorited) {
          await this.$axios.get('/api/favorite/delete', {
            params: {
              entityType: 'tiaojiPost',
              entityId: postId
            }
          })
          this.favorited = false
          this.$toast.success('已取消收藏！')
        } else {
          await this.$axios.get('/api/tiaoji/favorite/' + postId)
          this.favorited = true
          this.$toast.success('收藏成功')
        }
      } catch (e) {
        console.error(e)
        this.$toast.error('收藏失败：' + (e.message || e))
      }
    },
    share(href) {
      // this.title + '\n' + this.content + '\n    内容来源链接：' + this.home_detail_url
      this.$toast.success('分享')
    }
